import React, { memo } from 'react';
import { compose, withState, withHandlers, lifecycle } from 'recompose';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import FormControl from '@material-ui/core/FormControl';
import Checkbox from '@material-ui/core/Checkbox';
import FormLabel from '@material-ui/core/FormLabel';
import FormGroup from '@material-ui/core/FormGroup';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import MomentUtils from '@date-io/moment';
import moment from 'moment';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import _map from 'lodash/map';
import _size from 'lodash/size';
import _find from 'lodash/find';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import _clone from 'lodash/clone';
import Loading from '../../../components/Loading';
import SpaceBookingsService from '../../../services/space-bookings';
import SpaceCalendarsService from '../../../services/space-calendars';
import withRequest from '../../../utils/with-request';
import Tracker from '../../../utils/tracker';
import config from '../../../config';
import styles from './styles';

const minBookingDate = moment().add(1, 'day').toDate();
const initialSpaceBookingData = () => {
  return {
    title: '',
    busy: false,
    bookingDate: minBookingDate,
    periods: _clone([])
  };
};

const SpaceBookingModal = memo(({
  classes, open, setOpen, calendarLoading, isMonthView,
  bookingData, calendarFetch, submitBookingFetch, submitBookingLoading,
  cancelBookingFetch, cancelBookingLoading, readOnly,
  openModalButton, setBookingState, calendar, setCalendar
}) => {
  const handleOpen = async () => {
    try {
      setOpen(true);
      Tracker.hit('SpaceBookingModal - Viewed');
      await calendarFetch(true);
    } catch {
      setBookingState(initialSpaceBookingData());
    }
  };
  const handleClose = () => {
    Tracker.hit('SpaceBookingModal - Closed');
    if (!readOnly) {
      setBookingState(initialSpaceBookingData());
    }
    setOpen(false);
  };
  const refreshBookingDateCalendar = async (e) => {
    try {
      setCalendar([]);
      await calendarFetch(true);
      setBookingState({ bookingDate: e, periods: _clone([]) });
    } catch {
      setBookingState(initialSpaceBookingData());
    }
  };
  const onTogglePeriod = (e, checked) => {
    const { periods } = bookingData;
    try {
      const selectedPeriod = JSON.parse(e.currentTarget.value);
      if (checked) {
        periods.push(selectedPeriod);
        setBookingState({ periods });
      } else {
        setBookingState({ periods: periods.filter(p => p.label !== selectedPeriod.label) });
      }
    } catch {
      setBookingState(initialSpaceBookingData());
    }
  };
  const { id, title, bookingDate, periods, busy } = bookingData;
  const isLoading = submitBookingLoading || calendarLoading;
  const isEditable = id && readOnly;
  const isPastBooking = moment(bookingDate).isBefore();
  const canCreateBooking = !readOnly && _size(calendar);
  return (
    <React.Fragment>
      {
        openModalButton ?
          <div
            className={isMonthView ? classes.monthViewButton : classes.timeViewButton}
            onClick={handleOpen}
          >
            {openModalButton}
          </div>
          :
          <Button onClick={handleOpen} size="large" variant="contained" color="primary">
            Nova reserva
          </Button>
      }
      <Dialog open={Boolean(open)} maxWidth="md" onClose={handleClose} keepMounted>
        <DialogTitle>{readOnly ? title : 'Reservar espao'}</DialogTitle>
        <DialogContent className={classes.dialogContent}>
          {
            !readOnly &&
              <FormControl className={classes.inputField}>
                <TextField
                  fullWidth
                  label="Titlo"
                  margin="normal"
                  disabled={isLoading || readOnly}
                  InputLabelProps={{ shrink: true }}
                  value={title}
                  onChange={e => setBookingState({ title: e.target.value })}
                />
              </FormControl>
          }
          <FormControl className={classes.inputField}>
            <MuiPickersUtilsProvider
              libInstance={moment}
              utils={MomentUtils}
              locale={config.locale}
            >
              <DatePicker
                className={classes.calendarInputField}
                label="Data da reserva"
                invalidDateMessage="Formato invlido"
                variant="dialog"
                disabled={isLoading || readOnly}
                value={bookingDate}
                onChange={refreshBookingDateCalendar}
                minDate={minBookingDate}
                format="DD/MM/YYYY"
                cancelLabel="Cancelar"
                minDateMessage=""
              />
            </MuiPickersUtilsProvider>
          </FormControl>
          <FormControl className={classes.checkboxArea}>
            <FormLabel className={classes.inputLabel}>
              Horrios
            </FormLabel>
            {
              calendarLoading ?
                <div className={classes.loading}>
                  <Loading />
                </div>
                :
                <FormGroup>
                  {
                    isEditable ?
                      _map(periods, (p) => {
                        const label = `${moment(p.from).format('HH:mm')}-${moment(p.to).format('HH:mm')}`;
                        return (
                          <FormControlLabel
                            key={label}
                            label={label}
                            classes={{ label: classes.period }}
                            control={<Checkbox checked disabled />}
                          />
                        );
                      })
                      :
                      (
                        _size(calendar) ?
                          _map(calendar, (c) => {
                            const label = `${c.from}-${moment(c.to, 'HH:mm').add(1, 'minute').format('HH:mm')}`;
                            const disable = isLoading || readOnly || !c.available;
                            const labelClass = c.available ? classes.period : classes.periodBusy;
                            const check = disable ? false : !_isEmpty(
                              _find(periods, p => p.label === label)
                            );
                            return (
                              <FormControlLabel
                                key={label}
                                label={label}
                                classes={{ label: labelClass }}
                                control={
                                  <Checkbox
                                    color="primary"
                                    checked={check}
                                    disabled={disable}
                                    onChange={onTogglePeriod}
                                    value={JSON.stringify({ ...c, label })}
                                  />
                                }
                              />
                            );
                          })
                          :
                          <span className={classes.error}>
                            <br />
                            No h horrios disponveis nessa data
                          </span>
                      )
                  }
                </FormGroup>
            }
          </FormControl>
        </DialogContent>
        <DialogActions className={classes.center}>
          {
            (busy || isPastBooking) ?
              <Button onClick={handleClose} size="large">
                Fechar
              </Button>
              :
              (
                isEditable ?
                  <React.Fragment>
                    <Button
                      onClick={handleClose}
                      size="large"
                      disabled={cancelBookingLoading}
                    >
                      Fechar
                    </Button>
                    <Button
                      onClick={() => cancelBookingFetch(true)}
                      size="large"
                      color="secondary"
                      variant="contained"
                      disabled={cancelBookingLoading}
                    >
                      Remover reserva
                    </Button>
                  </React.Fragment>
                  :
                  <React.Fragment>
                    <Button onClick={handleClose} disabled={isLoading} size="large">
                      Fechar
                    </Button>
                    <Button
                      onClick={() => submitBookingFetch(true)}
                      size="large"
                      color="primary"
                      variant="contained"
                      disabled={isLoading || !canCreateBooking}
                      className={classes.submitButton}
                    >
                      {submitBookingLoading ? 'Reservando...' : 'Reservar'}
                    </Button>
                  </React.Fragment>
              )
          }
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
});

SpaceBookingModal.propTypes = {
  classes: PropTypes.object,
  open: PropTypes.bool,
  busy: PropTypes.bool,
  calendar: PropTypes.array,
  calendarFetch: PropTypes.func,
  calendarLoading: PropTypes.bool,
  setCalendar: PropTypes.func,
  setOpen: PropTypes.func,
  setBookingState: PropTypes.func,
  readOnly: PropTypes.bool,
  bookingData: PropTypes.object,
  submitBookingFetch: PropTypes.func,
  submitBookingLoading: PropTypes.bool,
  cancelBookingFetch: PropTypes.func,
  cancelBookingLoading: PropTypes.bool,
  isMonthView: PropTypes.bool,
  openModalButton: PropTypes.object
};

const withSpaceCalendar = withRequest(SpaceCalendarsService.getCalendar, {
  name: 'calendar',
  options: ({ space, setCalendar, readOnly, bookingData: { bookingDate } }) => ({
    variables: { spaceId: _get(space, 'id'), readOnly, bookingDate },
    onRequestFinished: (data) => {
      setCalendar(_get(data, 'space_calendars', []));
    },
    onRequestError: () => {
      setCalendar([]);
    }
  })
});

const withSubmitBookings = withRequest(SpaceBookingsService.createBookings, {
  name: 'submitBooking',
  init: false,
  options: ({ bookingData, calendarFetch, setBookingState, setOpen, space, onRequestBooking }) => ({
    variables: { ...bookingData, spaceId: _get(space, 'id') },
    onRequestFinished: () => {
      const trackProps = {};
      Tracker.hit('SpaceBooking - Added', trackProps);
      setBookingState(initialSpaceBookingData());
      setOpen(false);
      onRequestBooking();
    },
    onRequestError: (err) => {
      calendarFetch(false);
      setBookingState({ periods: _clone([]) });
      onRequestBooking(err);
    }
  })
});

const withCancelBooking = withRequest(SpaceBookingsService.cancelBooking, {
  name: 'cancelBooking',
  init: false,
  options: ({ bookingData, onRequestBooking, setOpen }) => ({
    variables: _get(bookingData, 'id'),
    onRequestFinished: () => {
      const trackProps = {};
      Tracker.hit('SpaceBooking - Canceled', trackProps);
      setOpen(false);
      onRequestBooking();
    },
    onRequestError: err => onRequestBooking(err)
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('open', 'setOpen', false),
  withState('bookingData', 'setBookingData', initialSpaceBookingData()),
  withState('calendar', 'setCalendar', []),
  withState('disableAction', 'setDisableAction', false),
  withHandlers({
    setBookingState: ({ setBookingData, bookingData }) => (attrs) => {
      setBookingData({ ...bookingData, ...attrs });
    }
  }),
  lifecycle({
    componentDidMount() {
      const { selectedBookingData, setBookingData, space } = this.props;
      if (selectedBookingData) {
        const { event: { id, title, from, to, bookingDate, busy } } = selectedBookingData;
        setBookingData({
          id,
          title,
          bookingDate,
          busy,
          spaceId: space.id,
          periods: [{ from, to, label: `${from}-${to}` }]
        });
      }
    }
  }),
  withSpaceCalendar,
  withCancelBooking,
  withSubmitBookings
)(SpaceBookingModal);
